import React, { useState, useEffect, useReducer } from "react";
import {
  Panel,
} from "@vkontakte/vkui";
import { AdminPanelHeader, AdminPanelMain } from "/";
import { NAME_PROJECT, NAME_PROJECT_LOWER_CASE } from "../../config";
import { SocketManager, formatCoinsToServer } from "../../helpers";
const { callMethod } = SocketManager;

const AdminPanel = ({ go, id, openAlert, snackbar }) => {
  const [DC, setDC] = useState("");
  const [userId, setUserId] = useState("");
  const [isBanned, setIsBanned] = useState(false);
  const [operation, setOperation] = useState(false);
  const [activeTab, setActiveTab] = useState("ban");

  function tab(type) {
    switch (type) {
      case "ban":
        setActiveTab(type);
        break;
      case "transfer":
        setActiveTab(type);
        break;
    }
  }

  function banUser() {
    if (!userId) {
      openAlert("–í—ã –Ω–µ –≤–≤–µ–ª–∏ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è", "red");
    }

    callMethod("banUser", {
      userId,
      isBanned,
    }).then(({ message, status }) => {
      if (status === "req_limit") return;
      openAlert(message, status === "denied" ? "red" : "green");
    });
  }

  function transfer() {
    let pornCoins = formatCoinsToServer(DC);
    callMethod("transfer", {
      pornCoins: operation ? pornCoins : -pornCoins,
      userId,
    }).then(({ message, status }) => {
      if (status === "req_limit") return;
      openAlert(message, status === "denied" ? "red" : "green");
    });
  }

  function onChangeAction(e, type) {
    const value = e.target.value.trim();

    switch (type) {
      case "userId":
        setUserId(value);
        break;
      case "DC":
        setDC(value);
        break;
    }
  }

  return (
    <Panel id={id} separator={false}>
      <AdminPanelHeader go={go} tab={tab} activeTab={activeTab} />
      <AdminPanelMain
        activeTab={activeTab}
        onChangeAction={onChangeAction}
        setIsBanned={setIsBanned}
        isBanned={isBanned}
        banUser={banUser}
        setOperation={setOperation}
        operation={operation}
        NAME_PROJECT={NAME_PROJECT}
        transfer={transfer}
      />

      {snackbar}
    </Panel>
  );
};

export { AdminPanel };
